.data-aos {
  --bg-parchment: #e8e6df;
  --bg-header: #0f2b38;
  --text-gold: #cfaa48;
  --text-dark: #1a1a1a;
  --text-light: #ffffff;
  --border-gold: #c5a059;
  --stat-wheel-gold: #8c7335;
  --stat-wheel-green: #3a5228;
  --card-scaling-factor: 1;
  font-family: "Cormorant Garamond", Georgia, serif;
  -webkit-font-smoothing: antialiased;
}
.data-aos.order {
  --bg-header: #0f2b38;
}
.data-aos.chaos {
  --bg-header: #2a1a1a;
}
.data-aos.death {
  --bg-header: #1a1a2a;
}
.data-aos.destruction {
  --bg-header: #2a2a1a;
}
.data-aos .warscroll {
  width: 650px;
  min-height: 900px;
  background-color: var(--bg-parchment);
  background-image: url("data:image/svg+xml,%3Csvg viewBox='0 0 200 200' xmlns='http://www.w3.org/2000/svg'%3E%3Cfilter id='noise'%3E%3CfeTurbulence type='fractalNoise' baseFrequency='0.65' numOctaves='3' stitchTiles='stitch'/%3E%3C/filter%3E%3Crect width='100%25' height='100%25' filter='url(%23noise)' opacity='0.08'/%3E%3C/svg%3E");
  position: relative;
  display: flex;
  flex-direction: column;
  transform: scale(var(--card-scaling-factor));
  transform-origin: top center;
  margin-bottom: 64px;
}
.data-aos .stat-wheel-wrapper {
  position: absolute;
  top: 8px;
  left: 8px;
  width: 130px;
  height: 130px;
  z-index: 20;
  filter: drop-shadow(2px 4px 6px rgba(0, 0, 0, 0.5));
}
.data-aos .stat-wheel-svg {
  width: 100%;
  height: 100%;
}
.data-aos .stat-label {
  font-family: "Cinzel", serif;
  font-size: 7px;
  fill: #ddd;
  text-anchor: middle;
  font-weight: 800;
  text-transform: uppercase;
}
.data-aos .stat-value {
  font-family: "Cinzel", serif;
  font-size: 14px;
  fill: #fff;
  text-anchor: middle;
  font-weight: 800;
  dominant-baseline: central;
}
.data-aos .rotate-90 {
  transform-box: fill-box;
  transform-origin: center;
  transform: rotate(90deg);
}
.data-aos .rotate-minus-90 {
  transform-box: fill-box;
  transform-origin: center;
  transform: rotate(-90deg);
}
.data-aos .stat-badges-wrapper {
  position: absolute;
  top: 8px;
  left: 8px;
  z-index: 20;
  filter: drop-shadow(2px 4px 6px rgba(0, 0, 0, 0.5));
}
.data-aos .stat-badges-row {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 4px;
}
.data-aos .core-stat-badge {
  background: #111;
  color: #fff;
  width: 60px;
  height: 52px;
